.ng2-c-table {
  display: block;
  visibility: hidden; }

.o-container .ng2-c-table {
  margin: 0 -10px; }

@media (min-width: 768px) {
  .ng2-c-table--gradient .ng2-c-table__row:nth-last-child(2) {
    opacity: 0.5; }
  .ng2-c-table--gradient .ng2-c-table__row:last-child {
    opacity: 0.25; }
  .ng2-c-table--gradient .ng2-c-table__group:not(:last-child) .ng2-c-table__row {
    opacity: 1; } }

.ng2-c-table--p-xs .ng2-c-table__header {
  padding: 0 5px 5px; }

.ng2-c-table--p-xs .ng2-c-table__header-column {
  padding: 5px 5px 0; }

.ng2-c-table--p-xs .ng2-c-table__column {
  padding: 10px 5px; }

.ng2-c-table__selectable-columns {
  text-align: right; }

.ng2-c-table__container {
  position: relative; }
  .ng2-c-table__container:not(.ng2-c-table__container--stacked) .ng2-c-table__item--show-stacked {
    display: none; }

.ng2-c-table__container--no-header .ng2-c-table__header-wrapper {
  display: none; }

.ng2-c-table__container--no-header .ng2-c-table__group:first-child {
  margin-top: 0; }

.ng2-c-table__container:not(.ng2-c-table__container--stacked) .ng2-c-table__group:first-child .ng2-c-table__group-title,
.ng2-c-table__container--grouped.ng2-c-table__container--stacked .ng2-c-table__group:first-child .ng2-c-table__group-title {
  display: none; }

.ng2-c-table__row {
  border-bottom: solid 1px rgba(204, 204, 204, 0.3);
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  padding: 0 5px;
  position: relative;
  width: 100%; }

.ng2-c-table__row--p-xs .ng2-c-table__column {
  padding: 5px; }

.ng2-c-table__placeholder {
  border-bottom: solid 1px rgba(204, 204, 204, 0.3);
  box-sizing: border-box;
  width: 100%; }

.ng2-c-table__column {
  color: #4a4a4a;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 15px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 0;
  opacity: 0;
  padding: 15px 5px; }
  .ng2-c-table__column:last-child {
    margin-bottom: 0; }

.ng2-c-table__column-header {
  display: none; }

.ng2-c-table__column--main {
  color: #28648a; }

.ng2-c-table__column--action {
  color: #28648a;
  text-align: right; }
  .ng2-c-table__column--action .icon-small-arrow-right {
    position: relative;
    top: 1px; }

.ng2-c-table__column--right {
  text-align: right; }

.ng2-c-table__help {
  cursor: pointer;
  overflow: visible;
  position: relative;
  line-height: inherit;
  margin: 0 0 0 5px;
  position: relative;
  transform: translateZ(0); }
  .ng2-c-table__help::after {
    background: transparent;
    content: '';
    display: block;
    height: 50px;
    left: 50%;
    min-width: 44px;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%); }

.ng2-c-table__header {
  background-color: rgba(204, 204, 204, 0.3);
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  padding: 0 5px 10px;
  position: relative;
  width: 100%;
  will-change: top; }

.ng2-c-table__header-wrapper {
  background-color: #f5f5f5;
  border-bottom: solid 1px rgba(204, 204, 204, 0.3); }

.ng2-c-table--sticky .ng2-c-table__header-wrapper {
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transform: translateZ(0);
  z-index: 2; }

.ng2-c-table--sticky .ng2-c-table__header-wrapper--fixed {
  margin: 0 auto;
  position: fixed; }

.body--is-ios .ng2-c-table--sticky .ng2-c-table__header-wrapper {
  position: sticky; }

.ng2-c-table__dropdown {
  color: #28648a;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 18px;
  margin-bottom: 5px;
  text-transform: uppercase;
  display: inline-block;
  padding: 10px 10px 0; }
  .ng2-c-table__dropdown::after {
    content: "\e90b";
    display: inline-block;
    font-family: "delen";
    font-size: 8px;
    margin-left: 5px;
    transform: scale(0.75); }

.ng2-c-table__header-column {
  color: #28648a;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 18px;
  margin-bottom: 5px;
  text-transform: uppercase;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  margin-bottom: 0;
  padding: 10px 5px 0; }
  .ng2-c-table__header-column:empty {
    display: none; }

.ng2-c-table__header-column--right {
  flex-direction: row-reverse;
  text-align: right; }
  .ng2-c-table__header-column--right .ng2-c-table__help {
    margin: 0 5px 0 0; }

.ng2-c-table__group-title {
  color: #28648a;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 18px;
  margin-bottom: 5px;
  text-transform: uppercase;
  background-color: rgba(204, 204, 204, 0.3);
  box-sizing: border-box;
  padding: 10px;
  position: relative;
  z-index: 1; }

.ng2-c-table__footer:not(:empty) {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 85px;
  padding: 15px 0; }

.ng2-c-table__container--stacked .ng2-c-table__column {
  justify-content: flex-start;
  order: 3;
  padding-bottom: 0;
  text-align: left; }

.ng2-c-table__container--stacked .ng2-c-table__column--main {
  justify-content: center;
  order: 1; }

.ng2-c-table__container--stacked .ng2-c-table__column--action {
  justify-content: center;
  order: 2;
  text-align: right; }

.ng2-c-table__container--stacked .ng2-c-table__row {
  padding-bottom: 15px; }

.ng2-c-table__container--stacked .ng2-c-table__column-header {
  color: #28648a;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 18px;
  margin-bottom: 5px;
  text-transform: uppercase;
  display: block; }

.ng2-c-table__container--stacked .ng2-c-table__column--main {
  color: #28648a; }

.ng2-c-table__container--stacked .ng2-c-table__column--main .ng2-c-table__column-header,
.ng2-c-table__container--stacked .ng2-c-table__column--action .ng2-c-table__column-header {
  display: none !important; }

.ng2-c-table__container--stacked:not(.ng2-c-table__container--grouped) .ng2-c-table__header-wrapper {
  padding-bottom: 10px; }
